import V from '../hyperscript'
import {useState} from 'react'
import './More'
import './File'
const styles = {
  BottomBox: {
    width: window.innerWidth,
    height: window.innerHeight - 40,
    background: "white"
  },
  Window: {
  	background: "white",
  	width: 400,
  	height: 600,
  	position: "absolute",
  	left: 200,
  	top: 200,
  	borderWidth: 0.5,
  	borderStyle:"solid",
  	borderColor:"black",
  	borderRadius:2,
  	display:"flex",
  	flexDirection:"column",
  	alignItems:'flex-start'
  },
	NewWindow:{
	margin:20,
	marginLeft:30,
	marginBottom:30,
	marginTop:0,
	borderWidth:0.5,
  	borderStyle:"solid",
  	borderColor:"black",
	paddingLeft:10,
	paddingRight:10,
	height: 30,
	background: "white",
	display: "flex",
	alignItems: "center",
	justifyContent: "center",
	borderRadius:2,
	cursor:"pointer"
  }
}
V.function("Window",a => {
	const [width,setW] = useState(400)
	const [height,setH] = useState(600)
	const [left,setX] = useState(a.x || Math.random() * 600)
	const [top,setY] = useState(a.y || 100)
	const [enableMove,setEm] = useState(false)
	const [maxEnableMove,setMaxEm] = useState(true)
	const [maxSt,setMs] = useState(false)
	const style = {
		...styles.Window,
		width,height,left,top
	}
	function windowMove(e){
		if(enableMove && maxEnableMove){
			e.preventDefault()
			setX(left+e.movementX)
			setY(top+e.movementY)
		}
	}
        function setLayer(e){
	    if(a.title!==a.arr[a.arr.length-1]){
	        let arr = a.arr.filter(item=>item!==a.title)
	        arr.push(a.title)
	        console.log("图层顺序为：："+arr)
		a.setArr(arr)
	    }
	    setEm(true)
        }
	function deleteSelf(e){
		let arr = a.arr.filter(item=>item!==a.title)
		a.setArr(arr)
	}
	function maxSelf(e){
		if(maxSt){
			setMs(false)
			setMaxEm(true)
			setX(a.x || Math.random() * 600)
			setY(a.y || 100)
			setW(400)
			setH(600)
		}
		else{
			setMs(true)
			setMaxEm(false)
			setX(0)
			setY(0)
			setW(window.innerWidth - 1) 
			setH(window.innerHeight - 1)
		}
	}
	return V.div({
			key:a.children,
			style,
			onMouseDown:e=>setLayer(e),
			onMouseUp:e=>setEm(false),
			onMouseMove:e=>windowMove(e),
			onMouseLeave:e=>setEm(false),
			onDoubleClick:e=>maxSelf(e)
		},
		[
			V.div({
				style:{
					width: width,
				  	display:"flex",
  					justifyContent:"space-between"
  				}
			},[
				V.div({
					style:{...styles.NewWindow,margin:0,marginLeft:1,marginBottom:0,borderLeftWidth:0,borderTopWidth:0},
					onClick:e=>deleteSelf(e)
				},"退出"),
				V.div({style:{...styles.NewWindow, borderTopWidth:0}},a.title),
				V.div({
					style:{...styles.NewWindow,margin:0,marginLeft:0,marginRight:1,marginBottom:0,borderRightWidth:0,borderTopWidth:0},
					onClick:e=>maxSelf(e)
				},"全屏")
			]),
			V.div({
				style:{
					marginTop:10,
					width:width - 1,
					height:height - 120,
	  				overflowY:"auto",
	  				overflowX:"hidden"
	  			}
			},a.children)
		]
	)
})
V.function("ListView",a => {
	const {st,setSt,menus,setMn,title} = a
	if(V[title]){
		return V[title]({st,setSt,menus,setMn,title})
	}
	else{
		return V.div("不存在")
	}
})
V.function("BottomBox",a => {
	const {menus,setMn,arr,setArr,st,setSt} = a
	return V.div({
  		style: styles.BottomBox
	},arr.map(title => V.Window(
		{key:title,arr,setArr,st,setSt,title},V.ListView({st,setSt,menus,setMn,title},title))))
})



