import V from '../../hyperscript'
import {useState,useEffect} from 'react'
V.function("文件视图",a => {
	const {
		key,
		type,
		pwd,
		storey,
		value,
		contextMenuState,
		setContextMenuState,
		contextMenuXy,
		setContextMenuXy
	} = a
	function contextMenu(e) {
		e.preventDefault()
		setContextMenuState({
			type,
			storey,
			pwd,
			value
		})
		setContextMenuXy([e.clientX,e.clientY])
	}
	return V.div({style:{},onContextMenu:e=>contextMenu(e)},a.value)
})


