import V from '../../hyperscript'
import {useState,useEffect} from 'react'
V.function("目录视图",a => {
	const {
		key,
		type,
		pwd,
		storey,
		value,
		contextMenuState,
		setContextMenuState,
		contextMenuXy,
		setContextMenuXy
	} = a
	const [subComponentVisible, setSubComponentVisible] = useState(false)
	const [color,setColor] = useState("red")
	const [editAble,setEditAble] = useState(false)
	function contextMenu(e) {
		e.preventDefault()
		setContextMenuState({
			type,
			storey,
			pwd,
			value
		})
		setContextMenuXy([e.clientX,e.clientY])
	}
	return V.div({},[
		editAble?
		V.input({
			value:a.value
		}):
		V.div({
			style:{
				cursor:"pointer",
				color
			},
			onMouseOver:e=>setColor("blue"),
			onMouseLeave:e=>setColor("red"),
			onClick:e=>setSubComponentVisible(!subComponentVisible),
			onContextMenu:e=>contextMenu(e)
		},a.value),
		subComponentVisible?V.div({
			style:{
				position:"relative",
				left:"20px"
			}
		},a.children):V.div()
	])
})


