import V from './hyperscript'
import {useState} from 'react'
import './BottomBox'
import './TopBox'
const styles = {
  Container: {
    display: "flex",
    width: window.innerWidth,
    height: window.innerHeight,
    background: "skyblue",
    alignItems: "center",
    justifyContent: "center",
    flexDirection: "column"
  }
}
V.function("Container", a => V.div({
  style: styles.Container
}, a.children))
V.function("App",a=>{
  const [menus,setMn] = useState(["文件","编辑","帮助"])
  const [arr,setArr] = useState([])
  const [st,setSt] = useState(true)
  return V.Container([
    V.TopBox({menus,arr,setArr,st,setSt}),
    V.BottomBox({menus,setMn,arr,setArr,st,setSt})
  ])
})
export default V.App;


