import V from '../../hyperscript'
import {useState} from 'react'
import {anyTrue} from '../../utils/anyTrue.js'
const styles = {
	NewWindow:{
	margin:20,
	marginLeft:30,
	marginBottom:30,
	marginTop:0,
	borderWidth:0.5,
  	borderStyle:"solid",
  	borderColor:"black",
	paddingLeft:10,
	paddingRight:10,
	height: 30,
	background: "white",
	display: "flex",
	alignItems: "center",
	justifyContent: "center",
	borderRadius:2,
	cursor:"pointer"
  }
}
V.function("更多",a => {
	const Menus = [
		"文件",
		"编辑",
		"视图",
		"跳转",
		"首选项",
		"代码编辑器",
		"帮助",
		"属性",
		"数据",
		"样式",
		"逻辑",
		"组件树",
		"login",
		"register"
	]
	function changeMenus(e,menu){
		let menus = a.menus
		if(a.st){
			a.setSt(false)
		}
		else{
			a.setSt(true)
		}
		if(anyTrue(menus,nowMenu => nowMenu === menu)){
			menus = menus.filter(nowMenu=>!(nowMenu === menu))
		}
		else{
			menus.push(menu)
		}
		a.setMn(menus)
	}
	return V.div({style:{
	  	display:"flex",
		flexDirection:"column",
  		justifyContent:"space-between",
  		alignItems:"center"
	}},Menus.map(menu => V.div({
		style:styles.NewWindow,
		onClick:e=>changeMenus(e,menu)
	},menu)))	
})


