import V from '../../hyperscript'
import {useState,useEffect} from 'react'
import {anyTrue} from '../../utils/anyTrue.js'
V.function("上下文菜单",a => {
	const [nextWindow,newNextWindow] = useState(false)
	const {
		contextMenuState,
		setContextMenuState,
		contextMenuXy,
		setContextMenuXy,
		tree,
		setTree
	} = a
	function deleteNode(e){
		function deleteThisNode(tree,pwd){
			if(tree.length === 0) {
				return []
			}
			if(anyTrue(tree, item => item.pwd === pwd)){
				return tree.filter(item => !(item.pwd === pwd))
			}
			else{
				return tree.map(item => {
					if(item.type === "file"){
						return item
					}
					else{
						return {
							storey: item.storey, 
							pwd: item.pwd,
							type: item.type,
							value: item.value,
							children: deleteThisNode(item.children, pwd)
						}
					}
				})
			}
		}
		setTree(deleteThisNode(
			tree,
			contextMenuState.pwd
		))
	}
	return V.div({style:{
		position:"fixed",
		left:0,
		top:0,
		width:1920,
		height:1080,		
		backgroundColor:"rgba(1,1,1,0.1)"
	},onClick:e=>setContextMenuState(false)},V.div({style:{
		position:"fixed",
		left:contextMenuXy[0],
		top:contextMenuXy[1],
		width:100,
		height:200,
		backgroundColor:"white"
	}},[
		V.div({
			style:{
				opacity:1
			}
		},"查看"),
		V.div({},"新建文件"),
		V.div({},"新建文件夹"),
		V.div({},"重命名"),
		V.div({
			style:{cursor:"pointer"},
			onClick:e=>deleteNode(e)
		},"删除")
	]))
})


