import V from '../hyperscript'
import {useState} from 'react'
import {anyTrue} from '../utils/anyTrue.js'
const styles = {
  TopBox: {
    width: window.innerWidth,
    height: 40,
    background: "white",
    display:"flex",
    alignItems: "center",
    justifyContent: "flex-start",
    flexDirection: "row"
  },
  NewWindow: {
  	paddingLeft:10,
  	paddingRight:10,
  	marginLeft:10,
  	height: 30,
  	background: "lightblue",
  	display: "flex",
  	alignItems: "center",
  	justifyContent: "center",
  	borderRadius:2,
  	cursor:"pointer"
  }
}
V.function("NewWindow",a => {
	const [background,setBg] = useState("lightblue")
	const style = {
    	...styles.NewWindow,
    	background
	}
	function newWindow(e){
		let arr = a.arr
		if(!anyTrue(arr,menu=>a.children===menu)){
			arr.push(a.children)
			a.setArr(arr)
		}
		if(a.st){
			a.setSt(false)
		}
		else{
			a.setSt(true)
		}
	}
	return V.div({
	    style,
	    onMouseEnter: e=>setBg("lightskyblue"),
	    onMouseLeave: e=>setBg("lightblue"),
	    onClick: e=>newWindow(e)
	},
	[
		...a.children
	])
})
V.function("TopBox",a => {
	const menus = a.menus
	const arr = a.arr
	const setArr = a.setArr
	const st = a.st
	const setSt = a.setSt
	return V.div({
		style: styles.TopBox
	},
	[
		...menus.map(menu=>V.NewWindow({arr,setArr,st,setSt},menu)),
		V.NewWindow({arr,setArr,st,setSt},"更多")
	])
})



