import V from '../../hyperscript'
import {useState,useEffect} from 'react'
import './contextmenu.js'
import './file.js'
import './dir.js'
V.function("文件",a => {
	const [tree,setTree] = useState([
		{
			"storey":0,
			"type":"folder",
			"pwd":"/hyperscript",
			"value":"hyperscript",
			"children":[
				{
					"storey":1,
					"pwd":"/hyperscript/src",
					"type":"folder",
					"value":"src",
					"children":[
						{
							"storey":2,
							"pwd":"/hyperscript/src/a.html",
							"type":"file",
							"value":"a.html"
						}
					]
				},
				{
					"storey":1,
					"pwd":"/hyperscript/index.js",
					"type":"file",
					"value":"index.js"
				},
				{
					"storey":1,
					"pwd":"/hyperscript/main.css",
					"type":"file",
					"value":"main.css"
				}
			]
		},
		{
			"storey":0,
			"pwd":"/react",
			"type":"folder",
			"value":"react",
			"children":[]
		},
		{
			"storey":0,
			"pwd":"/index.html",
			"type":"file",
			"value":"index.html"
		}
	])
	const [contextMenuState,setContextMenuState] = useState(false)
	const [contextMenuXy,setContextMenuXy] = useState([100,100])
	function createTree(tree){
		return tree.map(item => {
			if(item.type === "folder"){
				return V["目录视图"]({
					key:item.value,
					value:item.value,
					pwd:item.pwd,
					type:item.type,
					storey:item.storey,
					contextMenuState,
					setContextMenuState,
					contextMenuXy,
					setContextMenuXy
				},createTree(item.children))
			}
			if(item.type === "file"){
				return V["文件视图"]({
					storey:item.storey,
					key:item.value,
					pwd:item.pwd,
					type:item.type,
					value:item.value,
					contextMenuState,
					setContextMenuState,
					contextMenuXy,
					setContextMenuXy
				})
			}
		})
	}
	return V.div({},[...createTree(tree),contextMenuState && V["上下文菜单"]({
		contextMenuState,
		setContextMenuState,
		contextMenuXy,
		setContextMenuXy,
		tree,
		setTree
	})])
})


