export function anyTrue (arr,func) {
	for(let item of arr){
		if(func(item)){
			return true
		}
	}
}
export function allTrue (arr,func) {
	let n = arr.length - 1
	function all(arr,func,n) {
		if(!func(arr[n])){
			return false
		}
		else if(n === 0) {
			return true
		}
		else{
			return all(arr, func, n - 1)
		}
	}
	return all(arr,func,n)
}


